body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}
.App {
}

.App-Container {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

.App-Top-Nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    background: linear-gradient(to bottom, #00000099, #00000000);
    padding: 8px;
    color: white;
    font-size: 16px;
    font-weight: 500;
}

.App-Guide {
    position: absolute;
    bottom: 32px;
    left: 32px;
    width: 240px;
    padding: 16px;
    pointer-events: auto;
}

.circle:before {
    content: ' \25CF';
    font-size: 8px;
    color: #00fd00;
}

.dashboard {
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: auto;
    color: white;
    text-decoration: none;
}

.dashboard:hover {
    color: #ddd;
}

.dashboard:active {
    color: #ccc;
}

.fab-help {
    position: absolute;
    bottom: 32px;
    right: 16px;
    pointer-events: auto;
}

.link-discuss {
    font-size: 13px;
    margin-top: 8px;
}

